<template>
    <div class="container">
        <h1>{{ msg }}</h1>
        <p>
            For a guide and recipes on how to configure / customize this project,<br>
            check out the
            <a href="https://cli.vuejs.org" target="_blank" rel="noopener">vue-cli documentation</a>.
        </p>
        <h3>Installed CLI Plugins</h3>
        <ul>
            <li><a href="https://github.com/vuejs/vue-cli/tree/dev/packages/%40vue/cli-plugin-babel" target="_blank"
                   rel="noopener">babel</a></li>
            <li><a href="https://github.com/vuejs/vue-cli/tree/dev/packages/%40vue/cli-plugin-eslint" target="_blank"
                   rel="noopener">eslint</a></li>
        </ul>
        <h3>Essential Links</h3>
        <ul>
            <li><a href="https://vuejs.org" target="_blank" rel="noopener">Core Docs</a></li>
            <li><a href="https://forum.vuejs.org" target="_blank" rel="noopener">Forum</a></li>
            <li><a href="https://chat.vuejs.org" target="_blank" rel="noopener">Community Chat</a></li>
            <li><a href="https://twitter.com/vuejs" target="_blank" rel="noopener">Twitter</a></li>
            <li><a href="https://news.vuejs.org" target="_blank" rel="noopener">News</a></li>
        </ul>
        <h3>Ecosystem</h3>
        <ul>
            <li><a href="https://router.vuejs.org" target="_blank" rel="noopener">vue-router</a></li>
            <li><a href="https://vuex.vuejs.org" target="_blank" rel="noopener">vuex</a></li>
            <li><a href="https://github.com/vuejs/vue-devtools#vue-devtools" target="_blank"
                   rel="noopener">vue-devtools</a></li>
            <li><a href="https://vue-loader.vuejs.org" target="_blank" rel="noopener">vue-loader</a></li>
            <li><a href="https://github.com/vuejs/awesome-vue" target="_blank" rel="noopener">awesome-vue</a></li>
        </ul>
        <p>
            To view a demo online: <a href="https://codesandbox.io/s/mpklq49wp">https://codesandbox.io/s/mpklq49wp</a>
        </p>
        <div class="row">
            <div class="col-sm-4">
                <datepicker id="dateDeparture"
                            :format="customFormatter"
                            :full-month-name="true"
                            :disabled-dates="departurMinDate"
                            placeholder="Date"
                            calendar-class="my-calendar-box"
                            input-class="form-control"
                            wrapper-class="calendar-wrapper-box"
                            :clear-button="true"
                            clear-button-icon="fa fa-remove"
                            :calendar-button="true"
                            calendar-button-icon="fa fa-calendar"
                            calendar-button-icon-content="Show"
                            :required="true"
                            v-model="departureDate"
                            :open-date="departurMinDate.to"
                            @selected="dateSelectedFunction"
                ></datepicker>
            </div>
        </div>
        <h2>Clendar Example 2</h2>
        <div class="row">
            <div class="col-sm-4">
                <datepicker id="dateDeparture2"
                            :format="customFormatter"
                            :full-month-name="true"
                            :disabled-dates="departurMinDate"
                            placeholder="Date"
                            calendar-class="my-calendar-box"
                            input-class="form-control"
                            wrapper-class="calendar-wrapper-box"
                            :clear-button="true"
                            clear-button-icon="fa fa-remove"
                            :calendar-button="true"
                            calendar-button-icon="fa fa-calendar"
                            :bootstrap-styling="true"
                            :required="true"
                            v-model="departureDate2"
                            :open-date="departurMinDate.to"
                            @selected="dateSelectedFunction"
                ></datepicker>
            </div>
        </div>
    </div>
</template>

<script>
    import Datepicker from 'vuejs-datepicker'
    import moment from 'moment';

    export default {
        name: 'HelloWorld',
        components: {
            Datepicker,
        },
        props: {
            msg: String
        },
        data() {
            return {
                departureDate: '',
                departureDate2: '',
                departurMinDate: {
                    to: new Date(),
                },
            }
        },
        methods: {
            customFormatter(date) {
                let dobj = moment(date).format('YYYY-MM-DD');
                return dobj;
            },
            dateSelectedFunction(dateSelected) {
                this.departureDate = moment(dateSelected).format('YYYY-MM-DD');
            },
        }
    }
</script>

