<script>
    import Datepicker from 'vuejs-datepicker'
    import {
        getYYYYMMDD,
        addDaysToDate,
        minusDaysToDate,
        formatDate,
        getTime,
        getDifference,
        ensureTwoDigits,
        formatMoney,
        isLocalHost
    } from '../api/utils.js';
    import * as DAO from "../api/apiCalls";

    export default {
        name: 'Booking',
        components: {
            Datepicker,
        },
        data() {
            return {
                countries: [],
                flightData: {},
                searchData: {},
                from: '',
                submitted: false,
                to: '',
                toSearch: '',
                fromSearch: '',
                tripType: '1',
                notify_message: "",
                verifyError: "",
                departureDate: '',
                returnDate: '',
                cabinType: '5',
                adults: '1',
                childs: '0',
                infants: '0',
                form: {
                    adult_status: [],
                    adult_dob: [],
                    adult_nationality: [],
                    adult_first_name: [],
                    adult_last_name: [],
                    child_dob: [],
                    child_person_status: [],
                    child_last_name: [],
                    child_nationality: [],
                    child_first_name: [],
                    infant_nationality: [],
                    infant_dob: [],
                    infant_first_name: [],
                    infant_last_name: [],
                    email: '',
                    confirm_email: '',
                    phone: '',
                    other: '',
                },
            }
        },

        beforeCreate() {
            DAO.getCountries().then(response => {
                this.countries = response.data;
            });
        },
        beforeMount() {
            this.flightData = this.$store.getters.getFlight;
            this.searchData = this.$store.getters.getSearch;
            this.adults = parseInt(this.searchData.adults, 10);
            this.childs = parseInt(this.searchData.childs, 10);
            this.infants = parseInt(this.searchData.infants, 10);

            //console.log(this.searchData);
            //console.log(this.adults);
        },
        mounted() {
            //console.log(this.flightData)
        },
        methods: {
            getYYYYMMDD,
            addDaysToDate,
            minusDaysToDate,
            formatDate,
            getTime,
            getDifference,
            ensureTwoDigits,
            formatMoney,
            sumUpFare(totalFare) {
                return formatMoney(parseFloat(totalFare.equivFare + totalFare.totalAmount + totalFare.totalTax));
            },
            getUniqueId: function (prefix, outerIndex, innerIndex) {
                return prefix + '-' + (outerIndex + 1) + '-' + (innerIndex + 1);
            },
            submitFormData: function (e) {
                e.preventDefault();
                let inputValues = [];
                const nameInputs = Array.from(document.querySelectorAll('#formPassengers [name]'));
                nameInputs.forEach(function (i) {
                    inputValues.push({"fieldName": i.getAttribute("name"), "value": i.value});
                });
                console.log(inputValues);
                let _this = this;
                setTimeout(function () {
                    _this.$router.push("/thanks");
                }, 10000);
            },
        },
    }
</script>
