async function getAuthToken() {
    console.log("gettting token");
    const instance = axios.create({
        baseURL: `${sabreBaseURL}/`
    });
    instance.defaults.headers['Authorization'] = SabreKeyV2;
    instance.defaults.headers['grant_type'] = "client_credentials";
    let respData = await instance.post("v2/auth/token").then((response) => {
        // Handle success
        console.log("check response");
        console.log(response);
        if (response.status === 200) {
            return response;
        }
    }).catch((error) => {
        console.error("An error occurred.");
        console.error(error);
        return []
    }).finally(() => {
        //Perform action in always
    });
    console.log("check following");
    console.log(respData);
    return respData;
}

export async function searchFlights(params) {
    let seatsRequested = [];
    seatsRequested.push(params.adults + params.childs + params.infants);
    let dpDateObj = params.departureDate + "T00:00:00";
    let rtDateObj = params.returnDate + "T00:00:00";
    let requestData = {
        "OTA_AirLowFareSearchRQ": {
            "Version": "2",
            "AltLangID": "en-us",
            "AvailableFlightsOnly": true,
            "DirectFlightsOnly": false,
            "SeparateMessages" : false,
            "MaxResponses" : "1",
            "POS": {
                "Source": [
                    {
                        "PseudoCityCode": "HS5J",
                        "RequestorID": {
                            "Type": "1",
                            "ID": "1",
                            "CompanyName": {
                                "Code": "TN"
                            },
                        }
                    }
                ]
            },
            "OriginDestinationInformation": [],
            "TravelPreferences": {
                "Baggage" : {
                    "FreePieceRequired" : true,
                    "RequestType" : "N",
                },
                "CabinPref" : [
                    {
                        "Cabin" : "Economy",
                        "PreferLevel" : "Only"
                    }
                ],
                "ETicketDesired" : true,
                "MaxStopsQuantity" : 9,
                "TPA_Extensions": {
                    "DataSources": {
                        "ATPCO": "Enable",
                        "LCC": "Disable",
                        "NDC": "Disable"
                    },
                    "PreferNDCSourceOnTie": {
                        "Value": true
                    },
                    "NumTrips" : {
                        "Number" : 25
                    }
                }
            },
            "TravelerInfoSummary": {
                "AirTravelerAvail": [
                    {
                        "PassengerTypeQuantity": [
                            {
                                "Code": "ADT",
                                "Quantity": 1
                            }
                        ]
                    }
                ],
                "SeatsRequested" : [1],
            },
            "TPA_Extensions": {
                "IntelliSellTransaction": {
                    "RequestType": {
                        "Name": "200ITINS"
                    }
                },
            },
        },
    };
    requestData.OTA_AirLowFareSearchRQ.TravelPreferences.CabinPref.Cabin = getCabinValue(params.cabinType);
    requestData.OTA_AirLowFareSearchRQ.TravelerInfoSummary.SeatsRequested = seatsRequested;
    let depObject = {
        "RPH": "1",
        "DepartureDateTime": dpDateObj,
        "DestinationLocation": {
            "LocationCode": params.to
        },
        "OriginLocation": {
            "LocationCode": params.from
        },
        "TPA_Extensions" : {
            "Baggage" : {
                "FreePieceRequired" : true,
            },
        }
    };
    let retObject = {
        "RPH": "2",
        "DepartureDateTime": rtDateObj,
        "DestinationLocation": {
            "LocationCode": params.from
        },
        "OriginLocation": {
            "LocationCode": params.to
        },

    };
    requestData.OTA_AirLowFareSearchRQ.OriginDestinationInformation.push(depObject);
    if (params.tripType === 1) {
        requestData.OTA_AirLowFareSearchRQ.OriginDestinationInformation.push(retObject);
    }
    if(params.directFlights) {
        requestData.OTA_AirLowFareSearchRQ.TravelPreferences.MaxStopsQuantity = 0;
        requestData.DirectFlightsOnly = true;
    }
    let respData = await axios.post(`${sabreBaseURL}/v2/offers/shop`, requestData, {
        "headers": {
            "Content-Type": "application/json",
            "Authorization": `${AccessToken}`
        }
    }).then((response) => {
        // Handle success
        if (response.status === 200) {
            let d = response.data;
            if (d.groupedItineraryResponse.statistics.itineraryCount > 0) {
                return processSearchFlights(d.groupedItineraryResponse);
            } else {
                console.log(d.groupedItineraryResponse.messages);
                console.log("itinerary count is zero");
            }
            return [];
        } else {
            console.log("if else part");
        }
    }).catch((error) => {
        try {
            if (error.response) {
                let msg = JSON.parse(error.response.data.message);
                console.error("Error details");
                console.log(msg[0]);
                console.log(error.response);
            }
        } catch (e) {
            console.error(e);
        }
        return []
    }).finally(() => {
        //Perform action in always
    });
    return respData;
}

