<template>
    <section>
        <div class="container">
            <div v-if="loading">
                <div class="text-center">
                    <i class="fa fa-spinner fa-spin"></i>
                </div>
            </div>
            <div v-else class="row">
                <div class="col-sm-3">
                    <div class="bg-light filter-box">
                        <h2 class="title">Filter Your Results</h2>
                        <div class="filter-stop-over filter-set">
                            <h3 class="filter-label">Stop Over</h3>
                            <div class="form-group form-check">
                                <input type="checkbox" class="form-check-input stop" id="stopOverNone" value="0"
                                       v-on:change="filterResults">
                                <label class="form-check-label" for="stopOverNone">Non-Stop</label>
                            </div>
                            <div class="form-group form-check">
                                <input type="checkbox" class="form-check-input stop" id="stopOverOne" value="1"
                                       v-on:change="filterResults">
                                <label class="form-check-label" for="stopOverOne">1 Stop</label>
                            </div>
                            <div class="form-group form-check">
                                <input type="checkbox" class="form-check-input stop" id="stopOverOnePlus" value="2"
                                       v-on:change="filterResults">
                                <label class="form-check-label" for="stopOverOnePlus">1+ Stop</label>
                            </div>
                        </div>
                        <div class="filter-airlines filter-set">
                            <h3 class="filter-label">Airlines</h3>
                            <div class="form-group form-check" v-for="(al, alIndex) in filterAirlines" :key="alIndex">
                                <input type="checkbox" class="form-check-input airline"
                                       v-bind:id="getUniqueId('#alCheck',alIndex,0)" v-bind:value="al"
                                       v-on:change="filterResults">
                                <label class="form-check-label text-capitalize"
                                       v-bind:for="getUniqueId('#alCheck',alIndex,0)">{{getFlightName(al)}}</label>
                            </div>
                        </div>
                        <div class="filter-airports filter-set"></div>
                    </div>
                </div>
                <div class="col-sm-9 flights-listing-wrapper">
                    <div v-if="filtering">
                        <div class="text-center">
                            <i class="fa fa-spinner fa-spin"></i>
                        </div>
                    </div>
                    <div v-else-if="showFlights">
                        <div class="row">
                            <div class="col-sm-12 summary-wrapper">
                                <div class="row">
                                    <div class="col">
                                        <span class="info bold goldy">Showing {{flights.length}} Of {{allFlights.length}} Flights</span>
                                        <span class="info text-uppercase">{{getTripTypeName(sp.tripType)}} Flights</span>
                                    </div>
                                    <div class="col text-center">
                                        <span class="info goldy bold">{{sp.from}}</span>
                                        <span class="info bold">{{sp.departureDate}}</span>
                                    </div>
                                    <div class="col text-center">
                                        <span class="info goldy mt-2"><i class="fa fa-plane fa-45 fa-2x"></i></span>
                                    </div>
                                    <div class="col text-center">
                                        <span class="info goldy bold">{{sp.to}}</span>
                                        <span class="info bold">{{sp.departureDate}}</span>
                                    </div>
                                    <div class="col text-right">
                                        <span class="info bold goldy">Travellers</span>
                                        <span class="info">
                                                {{sp.adults}} <i class="fa fa-user"></i>
                                                {{sp.childs}} <i class="fa fa-user"></i>
                                                {{sp.infants}} <i class="fa fa-user"></i>
                                            </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="row flight-list-item" v-for="(flight, outerIndex) in flights" v-bind:data-ref="flight.id" :key="outerIndex">
                            <div class="col-sm-12 legs-row">
                                <div class="row">
                                    <div class="col-sm-2">
                                        <span class="info bold text-capitalize">{{ getFlightName(flight.FlightCode) }}</span>
                                        <span class="info"></span>
                                    </div>
                                    <div class="col-sm-8 itinerary-summary bg-light">
                                        <div class="row">
                                            <div class="col-sm-4 departure-column">
                                                <span class="info bold">{{ getAirportName(flight.summary.depPoint.departureAirport)}}</span>
                                                <span class="info">
                                                    <i class="fa fa-plane"></i>
                                                    {{flight.summary.depPoint.departureAirport}} - {{ getTime(flight.summary.depPoint.departureDateTime)}}</span>
                                            </div>
                                            <div class="col-sm-4 stoppage-column">
                                                <span class="info">
                                                    <span class="info" v-if="flight.summary.totalStops > 0">
                                                        <span class="info goldy">{{flight.summary.totalStops}} Stop(s)</span>
                                                        <span class="info separator">|</span>
                                                    </span>
                                                    <span class="info goldy">{{ getMinutesToString(flight.summary.totalTime) }}</span>
                                                </span>
                                                <span v-if="flight.summary.totalStops > 0">
                                                <span class="info stoppage"></span>
                                                <span class="info goldy">{{ flight.summary.stopNames.join(',') }}</span>
                                            </span>
                                                <span v-else class="info stoppage none"></span>
                                            </div>
                                            <div class="col-sm-4 arrival-column">
                                                <span class="info bold">{{getAirportName(flight.summary.arrPoint.arrivalAirport)}}</span>
                                                <span class="info">
                                                <i class="fa fa-plane fa-90"></i>
                                                {{flight.summary.arrPoint.arrivalAirport}} - {{getTime(flight.summary.arrPoint.arrivalDateTime)}}</span>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-sm-2 price-column">
                                        <span class="info bold price"><span class="goldy">{{flight.totalFare.currencyCode}}</span>&nbsp;{{ formatMoney(flight.totalFare.totalAmount)}}</span>
                                        <a v-on:click.stop.prevent="bookFlightNow(flight)"
                                           class="btn btn-primary book-now">Book Now</a>
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-12 flight-detail-wrapper bg-light">
                                <div class="row">
                                    <div class="col-sm-4">
                                        <a data-toggle="collapse"
                                           v-bind:href="getUniqueId('#flightInformation',outerIndex,0)"
                                           role="button" aria-expanded="false"
                                           v-bind:aria-controls="getUniqueId('flightInformation',outerIndex,0)">
                                            Flight Information <i class="fa fa-chevron-down"></i>
                                        </a>
                                    </div>
                                    <div class="col-sm-4 text-center">
                                        <span class="info mt-1"><i class="fa fa-briefcase"></i> {{ flight.baggage.bagWeight }}</span>
                                    </div>
                                    <div class="col-sm-4 text-right">
                                        <a data-toggle="collapse"
                                           v-bind:href="getUniqueId('#fareDetails',outerIndex,0)"
                                           role="button"
                                           aria-expanded="false"
                                           v-bind:aria-controls="getUniqueId('fareDetails',outerIndex,0)">
                                            Fare Details <i class="fa fa-chevron-down"></i>
                                        </a>
                                    </div>
                                </div>
                                <div class="row collapse" v-bind:id="getUniqueId('flightInformation',outerIndex,0)">
                                    <div class="card card-body flight-information">
                                        <div class="leg-details" v-for="(leg, legIndexA) in flight.legs"
                                             :key="legIndexA">
                                            <div class="row seg-details" v-for="(seg, rIndex) in leg.schedules"
                                                 :key="rIndex">
                                                <div class="col column-a">
                                                    <span class="info bold">{{getFlightName(seg.airlineCode)}}</span>
                                                    <span class="info">
                                                    {{seg.airlineCode}}&nbsp;{{ seg.flightNumber }}
                                                </span>
                                                    <span class="info">{{seg.departureAirportTerminal}}</span>
                                                </div>
                                                <div class="col column-b">
                                                    <span class="info">{{ getTime(seg.departureDateTime) }}</span>
                                                    <span class="info no-wrap">{{ formatDate(seg.departureDateTime) }}</span>
                                                    <span class="info">
                                                    {{ getAirportName(seg.departureAirport) }} ({{ seg.departureAirport }})
                                                </span>
                                                </div>
                                                <div class="col column-c">
                                                <span class="info text-center">
                                                    <span class="info">{{ getMinutesToString(seg.elapsedTime) }}</span>
                                                    <span class="info goldy"><i class="fa fa-plane fa-45"></i></span>
                                                </span>
                                                </div>
                                                <div class="col column-d">
                                                    <span class="info">{{ getTime(seg.arrivalDateTime) }}</span>
                                                    <span class="info no-wrap">{{ formatDate(seg.arrivalDateTime) }}</span>
                                                    <span class="info">{{ getAirportName(seg.arrivalAirport) }} ({{ seg.arrivalAirport }})</span>
                                                </div>
                                                <div class="col column-e">
                                                    <span class="info">Baggage</span>
                                                    <span class="info"><i class="fa fa-briefcase"></i> {{flight.baggage.bagWeight }}</span>
                                                </div>
                                                <div class="col-sm-12 lay-over"
                                                     v-html="getLayOver(leg.schedules, rIndex)"></div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="row collapse" v-bind:id="getUniqueId('fareDetails',outerIndex,0)">
                                    <div class="card card-body">
                                        <div class="row">
                                            <div class="col-sm-6 offset-sm-6">
                                                <table class="price-table table table-bordered">
                                                    <thead>
                                                    <tr>
                                                        <th>Fare Charges</th>
                                                        <th>Amount</th>
                                                    </tr>
                                                    </thead>
                                                    <tbody>
                                                    <tr>
                                                        <td>Base Amount</td>
                                                        <td>406.00</td>
                                                    </tr>
                                                    <tr>
                                                        <td>Tax Amount</td>
                                                        <td>156.70</td>
                                                    </tr>
                                                    </tbody>
                                                    <tfoot>
                                                    <tr>
                                                        <td>Total Fare</td>
                                                        <td>562.70</td>
                                                    </tr>
                                                    </tfoot>
                                                </table>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="d-none raw-data">
                                <div v-html="getItemData(flight.master)"></div>
                            </div>
                        </div>
                    </div>
                    <div v-else>
                        <h4 class="text-center text-uppercase">No Flight Found</h4>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
    import * as Cookies from 'js-cookie';
    import * as DAO from '../api/apiCalls.js';
    import * as Sabre from '../api/Sabre.js';

    import {
        getYYYYMMDD,
        addDaysToDate,
        minusDaysToDate,
        formatDate,
        getTime,
        getDifference,
        ensureTwoDigits,
        formatMoney,
        getFlightObject,
        getMinutesToString,
        isLocalHost
    } from '../api/utils.js';

    export default {
        name: 'FlightDetails',
        data() {
            return {
                allFlights: [],
                countries: [],
                flights: [],
                airLines: [],
                sp: {},
                loading: true,
                filtering: false,
                error: false,
                filterAirlines: []
            }
        },
        computed: {
            showFlights: function () {
                if (this.flights && this.flights.length > 0) {
                    return true;
                } else {
                    return false;
                }
            }
        },
        methods: {
            getYYYYMMDD,
            addDaysToDate,
            minusDaysToDate,
            formatDate,
            getTime,
            getDifference,
            getMinutesToString,
            ensureTwoDigits,
            formatMoney,
            isObject(item) {
                if ((!!item) && (item.constructor === Object)) {
                    return this.getItemData(item);
                } else if (Array.isArray(item)) {
                    return this.getItemData(item);
                } else {
                    return item;
                }
            },
            getTripTypeName(id) {
                switch (id) {
                    case 0:
                        return "One Way";
                    case 1:
                        return "Return";
                    default:
                        return id;
                }
            },
            getItemData(item) {
                let queryString = Object.keys(item).map(key => `<span style="display: inline-block; border: 1px dashed #999; padding: 5px; margin-right:10px;"><b>${key}</b> : ${this.isObject(item[key])}</span>`).join('');
                return queryString;
            },
            getAirportName(airportCode) {
                let airportCodeLC = airportCode.toLowerCase();
                let arr = this.$store.getters.getAirport.filter(o => o.iata !== null);
                let airports = arr.filter(o => o.iata.toLowerCase() === airportCodeLC);
                if (airports.length > 0) {
                    return airports[0].name;
                }
                return airportCode;
            },
            getUniqueId: function (prefix, outerIndex, innerIndex) {
                return prefix + '-' + (outerIndex + 1) + '-' + (innerIndex + 1);
            },
            getLayOver: function (schedules, index) {
                try {
                    let count = schedules.length;
                    if ((index + 1) === count) {
                        return "";
                    } else {
                        let next = index + 1;
                        let time = getDifference(schedules[next].departureDateTime, schedules[index].arrivalDateTime);
                        let city = schedules[index].arrivalCity;
                        return `<div class="info"><b>Layover</b>&nbsp;${time}&nbsp;|&nbsp;${city}</div>`;
                    }
                } catch (e) {
                    return "";
                }
            },
            dataToSend(flight) {
                return JSON.stringify(flight);
            },
            getFlightName(flightCode) {
                let match = this.airLines.filter(o => o.Code.toLowerCase() === flightCode.trim().toLowerCase());
                if (match.length > 0) {
                    return match[0].Name;
                }
                return flightCode;
            },
            getCountryName(countryCode) {
                let match = this.countries.filter(o => o.value.toLowerCase() === countryCode.toLowerCase());
                if (match.length > 0) {
                    return match[0].displayText;
                }
                return countryCode;
            },
            refresh(d) {
                alert("alert from flight details");
            },
            bookFlightNow(flightDetail) {
                let queryString = this.$route.query;
                if (Object.keys(queryString).length === 0) {
                    return;
                }
                let queryObject = getFlightObject(queryString);
                let segmentInfo = flightDetail.flightSegments[0];
                let pnrData = {
                    "ArrivalDT": segmentInfo.arrivalDateTime,
                    "DepartureDT": segmentInfo.departureDateTime,
                    "from": queryObject.from,
                    "to": queryObject.to,
                    "AirlineCode": flightDetail.FlightCode,
                    "FlightNumber": segmentInfo.flightNumber
                };
                //console.log("flightDetail");
                //console.log(JSON.stringify(flightDetail));
                //console.log(flightDetail);
                //console.log(pnrData);
                Sabre.createPNR(pnrData);
                /*
                                //define in store.js
                                this.$store.commit('setSearch', queryObject);
                                this.$store.commit('setFlightDetail', flightDetail);
                                this.$router.push("/booking");*/
            },
            filterResults() {
                this.filtering = true;
                let stops = [];
                let airlines = [];
                const checkedItems = Array.from(document.querySelectorAll(".filter-set .form-check-input:checked"));
                checkedItems.forEach(function (o) {
                    let oValue = o.value;
                    if (o.classList.contains("stop")) {
                        stops.push(oValue);
                    }
                    if (o.classList.contains("airline")) {
                        airlines.push(oValue);
                    }
                });

                let filteredFlights = [];
                let shouldMatch = 0;
                if (stops.length > 0) shouldMatch += 1;
                if (airlines.length > 0) shouldMatch += 1;
                if (shouldMatch > 0) {
                    for (let f = 0; f < this.allFlights.length; f++) {
                        let matchCount = 0;
                        let flight = this.allFlights[f];
                        let stopNum = parseInt(flight.summary.totalStops, 10);
                        //loop stops
                        for (let s = 0; s < stops.length; s++) {
                            let val = parseInt(stops[s], 10);
                            if (val < 2) {
                                if (stopNum === val) {
                                    matchCount += 1;
                                }
                            } else if (stopNum >= val) {
                                matchCount += 1;
                            }
                        }
                        let lines = airlines.filter(o => o === flight.FlightCode);
                        if (lines.length > 0) {
                            matchCount += 1;
                        }
                        if (matchCount === shouldMatch) {
                            filteredFlights.push(flight);
                        }
                    }
                    this.flights = filteredFlights;
                } else {
                    this.flights = this.allFlights;
                }
                let _this = this;
                setTimeout(function () {
                    _this.filtering = false;
                }, 50);
            },
            fetchData() {
                this.loading = true;
                let querySting = this.$route.query;
                let qp = getFlightObject(querySting);
                if (Object.keys(querySting).length === 0) {
                    this.loading = false;
                    return;
                }
                this.sp = qp;
                const checkDataAvailable = new Promise((resolve, reject) => {
                    if (isLocalHost) {
                        Sabre.getLocalFlightsData(qp).then(function (response) {
                            //console.log("3 flight details");
                            resolve(response);
                        });
                    } else {
                        Sabre.searchFlights(qp).then(function (response) {
                            //console.log("3 flight details");
                            resolve(response);
                        });
                    }
                });

                const haveFlights = (result) => {
                    //console.log("4 flight details");
                    this.loading = false;
                    this.error = false;
                    this.allFlights = result;
                    this.flights = result;
                    console.log(result);
                    let _this = [];
                    result.forEach(function (f) {
                        let fc = f.FlightCode;
                        if (_this.filter(o => o === fc) == 0) {
                            _this.push(fc);
                        }
                    });
                    this.filterAirlines = _this;
                    // eslint-disable-next-line no-undef
                    scrollToListing();
                };

                const noFlights = (result) => {
                    console.info(result);
                    this.loading = false;
                    this.error = true;
                };
                checkDataAvailable.then(haveFlights, noFlights);
            },
        },
        beforeCreate() {
            DAO.getAirlines().then(response => {
                this.airLines = response.data;
            });
            DAO.getCountries().then(response => {
                this.countries = response.data;
            });
        },
        mounted() {
            this.fetchData();
        },
        watch: {
            // call again the method if the route changes
            //'$route': 'fetchData'
        },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
    @import "../scss/FlightDetails.scss";
</style>
